@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-image: linear-gradient(v.$alpha 0%, v.$alpha 100%), url(../img/header.jpg);
    background-repeat: repeat, no-repeat;
    background-size: cover;
    background-position: 50%;
    padding: 3rem 0;
    @include m.desktop {
        padding-bottom: 15rem;
    }
    &__widget {
        margin-top: 6rem;
        @include m.desktop {
            margin-top: 12rem;
        }
    }
    &__texto {
        text-align: center;
        color: v.$blanco;
        font-size: 3rem;
        font-weight: 700;
    }
    &__boton {
        @include m.boton(v.$grisClaro, v.$secundario);
        text-align: center;
        width: fit-content;
        margin: 0 auto;
    }
}