body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

.fadeInFooter {
  animation: fadeInFooter 0.2s ease-in;
}

.fadeInBody {
  animation: fadeInBody 0.3s ease-out;
}

@keyframes fadeInFooter {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeInBody {
  0% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%), url(../img/header.jpg);
  background-repeat: repeat, no-repeat;
  background-size: cover;
  background-position: 50%;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .header {
    padding-bottom: 15rem;
  }
}
.header__widget {
  margin-top: 6rem;
}
@media (min-width: 1024px) {
  .header__widget {
    margin-top: 12rem;
  }
}
.header__texto {
  text-align: center;
  color: #FFFFFF;
  font-size: 3rem;
  font-weight: 700;
}
.header__boton {
  background-color: #e1e1e1;
  padding: 2rem 4rem;
  color: rgb(189, 30, 89);
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  cursor: pointer;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .nav {
    flex-direction: row;
  }
}
.nav__logo-contenedor {
  background-color: #FFFFFF;
  border-radius: 1rem;
  width: fit-content;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .nav__logo-contenedor {
    margin: 0;
  }
}
.nav__logo {
  width: 10rem;
  height: auto;
}
.nav__links {
  display: none;
}
@media (min-width: 768px) {
  .nav__links {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .nav__links {
    flex-direction: row;
  }
}
.nav__link {
  color: #FFFFFF;
  font-weight: 700;
  text-align: center;
  box-shadow: inset 0 -9px 0 0 rgb(189, 30, 89);
}
.nav__link:hover {
  box-shadow: inset 0 -9px 0 0 #e8505b;
}
.nav__link--boton {
  box-shadow: none;
  display: none;
}
.nav__link--boton:hover {
  box-shadow: none;
}
@media (min-width: 768px) {
  .nav__link--boton {
    display: block;
    background-color: #e1e1e1;
    padding: 2rem 4rem;
    color: rgb(189, 30, 89);
    margin-top: 1rem;
    border-radius: 3rem;
    font-size: 2rem;
    font-weight: 700;
    display: block;
    cursor: pointer;
    margin-top: 0;
    padding: 1rem 2rem;
    font-size: 1.6rem;
  }
}

.busqueda {
  margin: 5rem auto 0;
  max-width: 90rem;
}
@media (min-width: 1024px) {
  .busqueda {
    margin: 10rem auto 0;
  }
}
.busqueda__form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 5rem;
  box-shadow: 0 3px 7px 0px #686868;
  flex-direction: column;
  padding: 2rem;
}
@media (min-width: 1024px) {
  .busqueda__form {
    padding: 0;
    flex-direction: row;
  }
}
.busqueda__campo {
  width: 100%;
  padding: 1rem 2rem;
}
@media (min-width: 1024px) {
  .busqueda__campo:first-child {
    padding: 1rem 2rem 1rem 3rem;
  }
}
.busqueda__campo:hover {
  background-color: #e1e1e1;
  border-radius: 5rem;
  box-shadow: 0 3px 7px 0px #686868;
}
.busqueda__label {
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 900;
  margin-bottom: 5px;
}
.busqueda__input {
  border: none;
  color: #686868;
  width: 100%;
  border-radius: 5rem;
}
.busqueda__input:focus {
  background-color: #e1e1e1;
}
.busqueda__submit {
  background-color: rgb(189, 30, 89);
  padding: 2rem 4rem;
  color: #FFFFFF;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  cursor: pointer;
  border: none;
  margin: 0 2px 0 0;
  text-transform: uppercase;
  font-size: 1.6rem;
  width: 100%;
}
.busqueda__submit:hover {
  background-color: #e8505b;
}

.populares {
  padding: 5rem 0 0;
}
.populares__heading {
  line-height: 1;
  width: fit-content;
  color: rgb(189, 30, 89);
  box-shadow: inset 0 -9px 0 0 rgba(0, 0, 0, 0.3);
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .populares__grid {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .populares__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.populares__card {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .populares__card {
    justify-content: center;
  }
}
.populares__imagen {
  width: 14rem;
  height: auto;
}
.populares__info {
  margin: 0;
}

.alojamientos {
  padding: 5rem 0 0;
}
.alojamientos__heading {
  line-height: 1;
  width: fit-content;
  color: rgb(189, 30, 89);
  box-shadow: inset 0 -9px 0 0 rgba(0, 0, 0, 0.3);
  margin-bottom: 5rem;
}
.alojamientos__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  gap: 4rem;
  overflow-x: scroll;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
}
@media (min-width: 768px) {
  .alojamientos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    overflow-x: unset;
    overflow-y: unset;
    scroll-snap-type: unset;
  }
}
@media (min-width: 1024px) {
  .alojamientos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.alojamientos__card {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .alojamientos__card {
    scroll-snap-align: unset;
  }
}
.alojamientos__imagen {
  border-radius: 1rem;
}
.alojamientos__texto {
  text-align: center;
}

.anfitrion {
  margin: 5rem auto;
  max-width: 38rem;
  height: 70rem;
  background-image: url(../img/anfitrion.jpg);
  background-position: -40rem;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .anfitrion {
    margin: 5rem auto 0;
  }
}
@media (min-width: 768px) {
  .anfitrion {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    padding: 5rem;
    height: 45rem;
    max-width: 120rem;
    background-image: linear-gradient(to right, rgb(189, 30, 89) 0%, rgb(189, 30, 89) 40%, transparent 60%), url(../img/anfitrion.jpg);
    background-position: left, -2rem;
  }
}
@media (min-width: 1024px) {
  .anfitrion {
    background-position: left, 16rem;
  }
}
.anfitrion__box {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-image: linear-gradient(to top, rgb(189, 30, 89) 0%, rgb(189, 30, 89) 40%, transparent 55%);
  height: 75rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .anfitrion__box {
    background-image: unset;
    height: unset;
    justify-content: center;
    padding: unset;
  }
}
.anfitrion__heading {
  color: #FFFFFF;
  text-align: center;
  line-height: 1.2;
}
@media (min-width: 768px) {
  .anfitrion__heading {
    text-align: left;
  }
}
.anfitrion__info {
  color: #FFFFFF;
  margin: 3rem 0;
  line-height: 1.5;
}
.anfitrion__boton {
  background-color: #e1e1e1;
  padding: 2rem 4rem;
  color: rgb(189, 30, 89);
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  cursor: pointer;
  text-align: center;
  width: 100%;
  padding: 2rem;
}
@media (min-width: 768px) {
  .anfitrion__boton {
    width: fit-content;
  }
}

.experiencias {
  padding: 5rem 0 0;
}
.experiencias__heading {
  line-height: 1;
  width: fit-content;
  color: rgb(189, 30, 89);
  box-shadow: inset 0 -9px 0 0 rgba(0, 0, 0, 0.3);
  margin-bottom: 3rem;
}
.experiencias__texto {
  margin-bottom: 3rem;
}
.experiencias__grid {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  gap: 2rem;
  overflow-x: scroll;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
}
@media (min-width: 1024px) {
  .experiencias__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    overflow-x: unset;
    overflow-y: unset;
    scroll-snap-type: unset;
  }
}
.experiencias__card {
  scroll-snap-align: center;
}
@media (min-width: 1024px) {
  .experiencias__card {
    scroll-snap-align: unset;
  }
}
.experiencias__imagen {
  border-radius: 1rem;
}
.experiencias__info {
  font-weight: 900;
  font-size: 2rem;
}
.experiencias__info--texto {
  font-size: 1.8rem;
}

.footer {
  margin-top: 5rem;
  padding: 3rem 0;
  background-color: #e1e1e1;
}
.footer__bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 580px) {
  .footer__bar {
    flex-direction: row;
    align-items: flex-start;
    gap: 5rem;
  }
}
@media (min-width: 868px) {
  .footer__bar {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .footer__bar {
    flex-direction: row;
    gap: 0;
    justify-content: space-between;
  }
}
.footer__bar-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 580px) {
  .footer__bar-left {
    gap: 1rem;
  }
}
@media (min-width: 868px) {
  .footer__bar-left {
    flex-direction: row;
  }
}
.footer__bar-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 868px) {
  .footer__bar-right {
    flex-direction: row;
  }
}
.footer__name {
  border-bottom: 1px solid #000;
  padding-bottom: 2rem;
  display: block;
  width: 100%;
  text-align: center;
}
@media (min-width: 580px) {
  .footer__name {
    all: unset;
  }
}
.footer__left-sep {
  display: none;
}
@media (min-width: 868px) {
  .footer__left-sep {
    display: block;
  }
}
.footer__left-item, .footer__right-item {
  color: #000;
}
.footer__left-item:hover, .footer__right-item:hover {
  cursor: pointer;
  text-decoration: underline;
}
.footer__right-item:first-of-type {
  position: relative;
}
.footer__right-item:first-of-type::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/web.svg);
  background-repeat: no-repeat;
  position: absolute;
  top: -9px;
  left: -43px;
}
.footer__right-item:last-of-type {
  position: relative;
}
.footer__right-item:last-of-type::before {
  content: "";
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url(../img/flecha_arriba.png);
  background-repeat: no-repeat;
  background-size: 1.6rem;
  position: absolute;
  top: 0px;
  right: -24px;
}

.footer__modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  height: 100vh;
  width: 100%;
  flex-direction: column;
  justify-content: flex-end;
}
.footer__modal-box {
  background-color: #FEFEFE;
  border-top-left-radius: 3rem;
  border-top-right-radius: 3rem;
}
.footer__modal-box--scroll {
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .footer__modal-box--scroll {
    overflow-y: unset;
  }
}
.footer__modal-contenido {
  max-width: 1200px;
  margin: 0 auto;
  padding: 5rem;
  position: relative;
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1200px) {
  .footer__modal-contenido {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(4, 1fr);
    padding: 5rem 0;
  }
}
.footer__modal-contenido::after {
  content: "";
  display: block;
  width: 2rem;
  height: 2rem;
  background-image: url(../img/close.png);
  background-size: 2rem;
  background-repeat: no-repeat;
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
}
.footer__box-item {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}
.footer__box-item::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #c3c2c2;
  position: absolute;
  bottom: -24px;
}
.footer__box-item:last-of-type::after {
  display: none;
}
@media (min-width: 1200px) {
  .footer__box-item {
    gap: 2rem;
  }
  .footer__box-item::after {
    display: none;
  }
}
.footer__heading {
  font-size: 2rem;
}
.footer__nav {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 1rem;
}
@media (min-width: 768px) {
  .footer__nav {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 1rem;
  }
}
@media (min-width: 1024px) {
  .footer__nav {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 1rem;
  }
}
@media (min-width: 1200px) {
  .footer__nav {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
}
.footer__link {
  display: block;
  color: #666666;
}
.footer__link:hover {
  text-decoration: underline;
}